@import url(https://fonts.googleapis.com/css2?family=Ropa+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page--outer-container {
  width: 600px;
  height: 400px;
  background-color: #cee5e4;
  margin: 200px auto;
  box-sizing: border-box;
  font-family: "Ropa Sans", sans-serif;
}

.page--inner-container {
  width: 92%;
  height: 90%;
  background-color: #ffffff;
  display: inline-block;
  margin-left: 23px;
  margin-top: 20px;
}

.card--hidden-container {
  border-bottom: 2px black solid;
  margin: auto;
  margin-top: 80px;
  width: 80%;
  padding-bottom: 10px;
}

.card--flex-row {
  display: flex;
  justify-content: space-between;
  height: 10vh;
}

.card--flex-first-row {
  align-items: center;
  font-size: 18px;
  line-height: 20px;
}

img {
  width: 20%;
}

.card--width-first-row {
  width: 70%;
}

.card--author {
  margin-left: auto;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 12px;
}

.card--author p {
  margin: 0px;
}

/* .cards--container .slick-dots {
  width: 20%;
  bottom: 5px;
  margin: 0px;
  margin-left: 33px;
} */

.cards--container .slick-dots {
  position: absolute;
  width: 100px;
  left: 23px;
  bottom: 5px;
}

.cards--container .slick-dots li {
  margin-left: 0;
  margin-right: 0px;
  height: 12%;
  width: 12%;
}

.cards--container .slick-dots li button {
  padding: 0px;
}

.cards--btn {
  transition: 0.3s;
  margin: 0px 13px;
  padding: 5px 5px;
  border-radius: 50%;
}
.cards--btn:hover {
  background-color: rgb(193, 223, 249);
}

.cards--btn--container {
  margin-top: 20px;
  margin-left: 48px;
}

